*{
  background-color: rgb(169, 238, 59);
}
.header{
  padding: 12px 20px;
  text-align: center;
  font-size: 30px;
  margin-top:110px;

}
.Container{
 background-color: rgb(100, 84, 192);
 box-sizing: border-box;
 border-radius: 25px;
 margin:auto;
 padding: 20px;
 height: 450px;
 width: 50%;
 justify-content: center;
 text-align: center;
}

.input{
  height:0px;
  text-align: center;
  display: inline-block;
  
}
.input-box{
  height: 35px;
  width: 100%;
  margin-top: 8px;
  display: inline-block;
  border: 3px solid black;
  border-radius: 10px;
  background-color: antiquewhite;
  box-sizing: border-box;
}
.button{
  background-color:rgb(247, 98, 18);
  font-size: 15px;
  margin-top: 10px;
  margin-right: 5px;
  border-radius: 8px;
  width: 80px;
  height: 30px;
  cursor: pointer;
}
.button:hover:enabled{
  background-color: green;
  font-size: 15px;
  color: aliceblue;
}

.Listdata{
  margin-left: 10px;
  border-radius: 10px;
  margin-top: 9px;
  color: brown;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.list-heading{
  background-color: rgb(100, 84, 192);
  color: rgb(0, 0, 0);
  font-weight: 500;
  font-size: larger;
}
.listdatadiv{
   height: 40px;
   display: flex;
   border: 2px solid black;
   justify-content: space-between;
   border-radius: 20px;
   margin-bottom: 10px;
}
.btn-remove{
  margin-right: 15px;
  background-color:rgb(247, 98, 18);
  margin-top: 10px;
  border-radius: 5px;
  width: 50px;
  height: 20px;
}
.btn-remove:hover{
  color:white;
  background-color: red;
  border:none;

}
.remove-all{
  margin-left: 5px;
  justify-content: center;
  text-align: center;
  border-radius: 8px;
  height: 30px;
  width: 80px;
  cursor: pointer;
  background-color: rgb(242, 145, 0);
  
}
.remove-all:hover:enabled{
  color:white;
  background-color: red;
  border:none;
}
